.showcase {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: calc(300 * var(--unit-length));
  padding: calc(16 * var(--unit-length));
  border-radius: calc(8 * var(--unit-length));
  background-color: var(--s-color-bg-dark-2);
}

.info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.info__left {
  flex: 1;
  overflow: hidden;
}

.title {
  overflow: hidden;
  color: var(--s-color-text-light-1);
  font-weight: bold;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.tags {
  overflow: hidden;
  width: fit-content;
  background: linear-gradient(90deg, #00f260 0%, #40e0d0 100%);
  background-clip: text;
  color: transparent;
  font-size: var(--s-font-size-small);
  text-overflow: ellipsis;
  white-space: nowrap;
}

.category {
  padding: calc(4 * var(--unit-length)) calc(10 * var(--unit-length));
  border-radius: calc(4 * var(--unit-length));
  background: #40e0d0;
  text-align: center;
}

.desc {
  overflow: hidden;
  color: var(--s-color-text-light-2);
  font-size: var(--s-font-size-small);
  text-overflow: ellipsis;
  white-space: nowrap;
}

.frame__wrapper {
  position: relative;
  flex: 1;
  overflow: hidden;
  margin-bottom: calc(8 * var(--unit-length));
  border-radius: calc(8 * var(--unit-length));
}
.frame__wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  display: none;
  width: calc(200 * var(--unit-length));
  height: calc(150 * var(--unit-length));
  background: linear-gradient(to top right, transparent 50%, rgba(0, 0, 0, 0.5) 100%);
}

.frame__wrapper:hover::after {
  display: block;
}

.frame__wrapper--disactive {
  pointer-events: none;
}

.frame {
  min-height: 100%;
  background: var(--s-color-bg-light-1);
}

.frame__btn {
  position: absolute;
  top: calc(20 * var(--unit-length));
  right: calc(20 * var(--unit-length));
  z-index: 10;
  display: none;
  width: calc(28 * var(--unit-length));
  height: calc(28 * var(--unit-length));
  border-radius: calc(6 * var(--unit-length));
  background: var(--s-color-bg-dark-5);
  font-size: calc(24 * var(--unit-length));
  line-height: calc(28 * var(--unit-length));
  text-align: center;
  user-select: none;
}
.frame__btn:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.frame__wrapper:hover .frame__btn {
  display: block;
}

.dialog__body .frame {
  flex: 1;
  overflow: auto;
  min-width: 0;
  max-height: 100%;
}

.dialog {
  overflow: auto;
}

.dialog :global(.dialog__inner) {
  width: calc(1200 * var(--unit-length));
  max-width: 100%;
  border-radius: calc(8 * var(--unit-length));
  background: var(--s-color-bg-dark-1);
}

.dialog__header {
  padding: calc(8 * var(--unit-length));
}

.dialog__body {
  display: flex;
  height: 80vh;
}

.tabs {
  padding: calc(8 * var(--unit-length));
  background: var(--s-color-bg-dark-3);
}

.code__wrapper {
  flex: 1;
  overflow: auto;
  min-width: 0;
  min-height: 100%;
  background: var(--s-color-bg-dark-4);
}
