*:where(:not(html, iframe, canvas, img, svg, video, audio, svg *, symbol *)) {
  all: unset;
  display: revert;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  text-size-adjust: none;
}

a,
button {
  cursor: revert;
}

ol,
ul,
menu,
summary {
  list-style: none;
}

img {
  max-inline-size: 100%;
  max-block-size: 100%;
}

table {
  border-collapse: collapse;
}

input,
textarea {
  user-select: auto;
}

textarea {
  white-space: revert;
}

meter {
  appearance: revert;
}

:where(pre) {
  all: revert;
  box-sizing: border-box;
}

::placeholder {
  color: unset;
}

:where([hidden]) {
  display: none;
}

:where([contenteditable]:not([contenteditable=false])) {
  -moz-user-modify: read-write;
  -webkit-user-modify: read-write;
  overflow-wrap: break-word;
  line-break: after-white-space;
  user-select: auto;
}

:where([draggable=true]) {
  -webkit-user-drag: element;
}

:where(dialog:modal) {
  all: revert;
  box-sizing: border-box;
}

pre {
  margin: unset;
}

body {
  font-weight: normal;
  font-size: var(--s-font-size-normal);
  font-family: JetBrainsMono, "Microsoft YaHei", "PingFang SC";
}

html {
  --unit-length: calc(1rem / 16);
}

::-webkit-scrollbar {
  width: calc(10 * var(--unit-length));
  height: calc(10 * var(--unit-length));
}

::-webkit-scrollbar-track {
  background-color: var(--s-color-bg-dark-1);
}

::-webkit-scrollbar-thumb {
  border-radius: calc(5 * var(--unit-length));
  background-color: var(--s-color-text-light-2);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--s-color-bg-dark-5);
}
