.tabs {
  display: flex;
  gap: calc(2 * var(--unit-length));
  border-radius: calc(2 * var(--unit-length));
}

.tab {
  height: calc(36 * var(--unit-length));
  padding-inline: calc(8 * var(--unit-length));
  background: var(--s-color-bg-dark-1);
  color: var(--s-color-text-light-1);
  line-height: calc(36 * var(--unit-length));
  text-align: center;
  cursor: pointer;
  user-select: none;
}

.tab:hover {
  opacity: 0.6;
}

.tab--active {
  background: var(--s-color-text-light-2);
  color: unset;
}
