@keyframes come-out {
  0% {
    top: calc(20 * var(--unit-length));
  }
  80% {
    top: calc(20 * var(--unit-length));
  }
  100% {
    top: calc(-30 * var(--unit-length));
  }
}
.notify__wrapper {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  animation: come-out 2s;
}

.notify {
  width: fit-content;
  min-width: calc(160 * var(--unit-length));
  max-width: calc(600 * var(--unit-length));
  padding: calc(8 * var(--unit-length)) calc(12 * var(--unit-length));
  border: calc(1 * var(--unit-length)) solid;
  border-radius: calc(2 * var(--unit-length));
  text-align: center;
}

.notify--success {
  border-color: #25371c;
  background-color: #1c2518;
  color: #67c23a;
}

.notify--warning {
  border-color: #3e301c;
  background-color: #292218;
  color: #e6a23c;
}

.notify--error {
  border-color: #412626;
  background-color: #2b1d1d;
  color: #f56c6c;
}

.notify--info {
  border-color: #2d2d2f;
  background-color: #202121;
  color: #909399;
}
