.main {
  height: 100%;
  padding: calc(12 * var(--unit-length));
  background-color: var(--s-color-bg-dark-3);
}

.showcases {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(calc(400 * var(--unit-length)), 1fr));
  gap: calc(20 * var(--unit-length));
  margin: calc(20 * var(--unit-length)) 0;
}

:global(.showcase) {
  align-self: center;
  justify-self: center;
}
