.nav {
  height: 100%;
  background: var(--s-color-bg-dark-2);
}

.title {
  padding-block: calc(20 * var(--unit-length));
  background: linear-gradient(45deg, #00f260 0%, #0575e6 100%);
  background-clip: text;
  color: transparent;
  font-weight: bold;
  font-size: var(--s-font-size-middle);
  text-align: center;
}

@property --color-stop1 {
  syntax: "<color>";
  inherits: false;
  initial-value: #40e0d0;
}
@property --color-stop2 {
  syntax: "<color>";
  inherits: false;
  initial-value: #ff8c00;
}
@property --color-stop3 {
  syntax: "<color>";
  inherits: false;
  initial-value: #ff0080;
}
@keyframes change-color {
  0% {
    --color-stop1: #40e0d0;
    --color-stop2: #ff8c00;
    --color-stop3: #ff0080;
  }
  33% {
    --color-stop1: #ff0080;
    --color-stop2: #40e0d0;
    --color-stop3: #ff8c00;
  }
  66% {
    --color-stop1: #ff8c00;
    --color-stop2: #ff0080;
    --color-stop3: #40e0d0;
  }
  100% {
    --color-stop1: #40e0d0;
    --color-stop2: #ff8c00;
    --color-stop3: #ff0080;
  }
}
.slogan {
  width: calc(140 * var(--unit-length));
  height: calc(50 * var(--unit-length));
  margin-inline: auto;
  border-radius: calc(4 * var(--unit-length));
  font-size: var(--s-font-size-small);
  user-select: none;
}

@supports (--css: variable) {
  .slogan {
    background-image: radial-gradient(circle at 50% 50%, var(--color-stop1) 0%, var(--color-stop2) 50%, var(--color-stop3) 100%), linear-gradient(90deg, var(--color-stop1) 0%, var(--color-stop2) 50%, var(--color-stop3) 100%);
    animation: change-color 4s linear infinite;
  }
}
@supports not (--css: variable) {
  .slogan {
    background-image: linear-gradient(90deg, #40e0d0 0%, #ff8c00 50%, #ff0080 100%);
  }
}
.slogan__inner {
  display: inline-block;
  width: calc(100% - calc(6 * var(--unit-length)));
  height: calc(100% - calc(6 * var(--unit-length)));
  margin: calc(3 * var(--unit-length));
  background-color: var(--s-color-bg-dark-4);
  color: var(--s-color-text-light-1);
  line-height: calc(46 * var(--unit-length));
  text-align: center;
}

.entries {
  margin-top: calc(20 * var(--unit-length));
}

.entry {
  height: calc(36 * var(--unit-length));
  padding-inline: calc(20 * var(--unit-length));
  color: var(--s-color-text-light-1);
  line-height: calc(36 * var(--unit-length));
}
.entry:hover {
  background-color: var(--s-color-bg-dark-3);
}
