html {
  --s-color-bg-light-1: #ffffff;
  --s-color-bg-dark-1: #2c303b;
  --s-color-bg-dark-2: #1e1e26;
  --s-color-bg-dark-3: #131418;
  --s-color-bg-dark-4: #010101;
  --s-color-bg-dark-5: #74829a;
  --s-color-text-light-1: #ffffff;
  --s-color-text-light-2: #c7c9d3;
  --s-color-text-dark: #000000;
}

html {
  --s-font-size-large: calc(45 * var(--unit-length));
  --s-font-size-middle: calc(27 * var(--unit-length));
  --s-font-size-normal: calc(16 * var(--unit-length));
  --s-font-size-small: calc(14 * var(--unit-length));
  --s-font-size-tiny: calc(12 * var(--unit-length));
}

@font-face {
  font-weight: normal;
  font-style: normal;
  font-family: JetBrainsMono;
  src: url("@/asset/font/JetBrainsMono-Regular.woff2") format("woff2");
}
@font-face {
  font-weight: bold;
  font-style: normal;
  font-family: JetBrainsMono;
  src: url("@/asset/font/JetBrainsMono-Bold.woff2") format("woff2");
}
@font-face {
  font-weight: bold;
  font-style: italic;
  font-family: JetBrainsMono;
  src: url("@/asset/font/JetBrainsMono-BoldItalic.woff2") format("woff2");
}
