.layout {
  display: grid;
  grid-template-areas: "nav main" "nav footer";
  grid-template-rows: 1fr calc(100 * var(--unit-length));
  grid-template-columns: calc(180 * var(--unit-length)) 1fr;
  overflow: hidden;
  min-height: max(100vh, calc(840 * var(--unit-length)));
}

.layout--nav-collapsed {
  grid-template-columns: calc(16 * var(--unit-length)) 1fr;
}

.main {
  grid-area: main;
}

.footer {
  grid-area: footer;
}

.nav {
  position: relative;
  grid-area: nav;
}

@keyframes dyn-bg {
  100% {
    transform: translateY(calc(-17 * var(--unit-length)));
  }
}
.nav-collapsed {
  background-image: repeating-linear-gradient(45deg, var(--s-color-bg-dark-1), var(--s-color-bg-dark-1) calc(2 * var(--unit-length)), var(--s-color-bg-dark-3) 0, var(--s-color-bg-dark-3) calc(4 * var(--unit-length)));
  cursor: pointer;
  transition: opacity 0.6s ease;
}
.nav-collapsed:hover {
  height: calc(100% + calc(17 * var(--unit-length)));
  opacity: 0.8;
  animation: dyn-bg 0.6s infinite linear;
}

.collapse-btn {
  position: absolute;
  top: 5%;
  right: calc(-30 * var(--unit-length));
  width: calc(30 * var(--unit-length));
  height: calc(30 * var(--unit-length));
  border-radius: calc(4 * var(--unit-length));
  background-color: var(--s-color-bg-dark-1);
  font-size: calc(20 * var(--unit-length));
  line-height: calc(30 * var(--unit-length));
  text-align: center;
  visibility: hidden;
  cursor: pointer;
  transition: visibility 0.6s ease;
}
.collapse-btn:hover {
  opacity: 0.8;
}

.nav:hover .collapse-btn {
  visibility: visible;
}
